import React, { useState } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import { EventAPI } from "../../../commons/EventAPI";

const dispatchedEvents = [];

const SrsMuiSelect = function (props, ref) {
  const [selectedValue, setSelectedValue] = useState(props.selected || "");

  const handleChange = (evt) => {
    let selectedValue = evt.target.value;
    setSelectedValue(selectedValue);
    props.dispatchEvent(EventAPI.MUI_SELECT_CHANGED, { payload: {id: props.id || props.elementid, value: selectedValue}});
  }

  let options = [];
  props.items?.map((item) => {
    options.push(<MenuItem value={item.value}>{item.label}</MenuItem>);
  });
  
  let result = (<FormControl fullWidth={props.fullWidth}>
    <InputLabel id="select-label">{props.label}</InputLabel>
    <Select
      {...props}
      labelId={props.label}
      id="simple-select"
      value={selectedValue}
      label={props.label}
      onChange={handleChange}
      theme={props.muiTheme}
    >
     {options} 
    </Select>
  </FormControl>)
  return result;
};

const styles = `
  /* Custom styles for the MuiSelect component */
`;

const props = [
  {
    name: "label",
    defaultValue: "",
    type: "string"
  },
  {
    name: "selected",
    defaultValue: "",
    type: "string"
  },
  {
    name: "items",
    defaultValue: [],
    type: "object"
  },
  {
    name: "native",
    defaultValue: false,
    type: "boolean"
  },
  {
    name: "autoWidth",
    defaultValue: false,
    type: "boolean"
  },
  {
    name: "displayEmpty",
    defaultValue: false,
    type: "boolean"
  },
  {
    name: "fullWidth",
    defaultValue: false,
    type: "boolean"
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: ["srs-select-option"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiSelect),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
